import React, { useState } from 'react';
import StartButton from '../components/startButton';
import loseSound from '../game-suonds/Wrong-answer-sound-effect.mp3'
import winSound from '../game-suonds/correct-answer.mp3'
import Quiz from '../components/Quiz'
import {Link} from 'react-router-dom'
function GamePage() {
  const [letter, setLetter] = useState()
  const [answer, setAnswer] = useState()
  const [userAnswer, setUserAnswer] = useState()
  const [gameStatus, setGameStatus] = useState("")
  const [totalAnswer, setTotalAnswer] = useState(0)
  const [correctAnswer, setCorrectAnswer] = useState(0)
  const [wrongAnswer, setWrongAnswer] = useState(0)
  const quizArray = [ 
    ['j','g','p','q','y'],
    ['b','d','f','h','l','k','t'],
    ['a','c','e','i','m','n','o','r','s','u','v','w','x','z']
  ]; // ends quiz array
 
  const PICK_A_Letter = ()=>{
    const randomNumber = Math.floor(Math.random() * 3)
    setLetter(quizArray[randomNumber][Math.floor(Math.random() * quizArray[randomNumber].length)])
    setAnswer(randomNumber)
  } // ends pick a letter method
  const checkAnswer = ()=>{
    parseInt(userAnswer) === answer ? setGameStatus("win") : setGameStatus("lose")

  } // ends check answer function
  const PARFORM_AFFTER_STEPS = ()=>{
    checkAnswer()
    updateStatus()
    PLAY_SOUND()
    setTimeout(()=>{
      setGameStatus("playing")
    },2000)
    PICK_A_Letter()
  } // ENDS PARFORM AFFTER STEPS FUNCTION
  function getClassName(){
   if(gameStatus === "" || gameStatus === "playing"){ return "default"}
   else if(gameStatus === "lose"){ return "wrong"}
   else{ return "right"}

  } // ends getclassname function 
  const PLAY_SOUND = ()=>{
    parseInt(userAnswer) !== answer ? new Audio(loseSound).play() : new Audio(winSound).play() 
  } // ENDS PLAYSOUND FUNCTION
  const updateStatus = ()=>{
    if(parseInt(userAnswer) == answer){
      setTotalAnswer(totalAnswer+1)
      setCorrectAnswer(correctAnswer+1)
    } else if(parseInt(userAnswer) != answer){
      setTotalAnswer(totalAnswer+1)
      setWrongAnswer(wrongAnswer+1)
    }
  } // ends updatestatus function
 
 
  return (
    <main className={getClassName()}>
      <div className='container'>
        <div className='status-div'>
        <h3>Status</h3>
        <table>
          <tr>
            <td>Total Answers</td>
            <td>{totalAnswer}</td>
          </tr>
          <tr>
            <td>Correct Answers</td>
            <td>{correctAnswer}</td>
          </tr>
          <tr>
            <td>Wrong Answers</td>
            <td>{wrongAnswer}</td>
          </tr>
        </table>
        <Link className='learn-button' to="/react-quiz-game/infopage">Learn about the letters</Link>
        </div>
        <section className='info'>
          <h3>Let's test your self</h3>
          <div className={`line ${getClassName()}`}></div>
          {gameStatus === "" ? <span onClick={()=>{
            setGameStatus("playing")
            PICK_A_Letter()
          }}><StartButton/></span> : <Quiz letter={letter} setUserAnswer={setUserAnswer} PARFORM_AFFTER_STEPS={PARFORM_AFFTER_STEPS} getClassName={getClassName}/>}
        </section>
      </div>
    </main>
  );
}

export default GamePage;
