import React,{useRef,useState,useEffect} from 'react'
import {Link} from 'react-router-dom'
import {GiHamburgerMenu} from 'react-icons/gi'

function InfoPage() {
          useEffect(()=>{
                    window.innerWidth <= 992 && setHide(true)
          },[])
          const [hide, setHide] = useState(false)
          const grassLetter = useRef(null)
          const skyLetter = useRef(null)
          const rootLetter = useRef(null)
          
          return (
                    <main className='default info-page'>
                              
                              <div className='container'>
                              <span className='menu' onClick={()=> setHide(!hide)}><GiHamburgerMenu/></span>
                                        <div className="side" style={{display: hide ? "none" : "block"}}>
                                                  <a  onClick={()=> grassLetter.current?.scrollIntoView()}>Grass Letters</a>
                                                  <a  onClick={()=> skyLetter.current?.scrollIntoView()}>Sky Letters</a>
                                                  <a  onClick={()=> rootLetter.current?.scrollIntoView()}>Root Letters</a>
                                                  <Link to="/react-quiz-game" className='back-button'>Go back</Link>
                                        </div >
                                        <div className="info-content">
                                                  <h3>GRASS, ROOT AND SKY LETTERS
                                                  </h3>
                                                  <div className='line default'></div>
                                                  <p>The lower case or small letters of the alphabets have different shapes and are “positioned” differently when written on the four lined paper. The children in a Montessori classroom are shown how to differentiate between them according to their shape and position on the four lines by giving separate names to similar shaped and position of the letters.
                                                  </p>
                                                  <p>In order to improve child’s awareness regarding the shape and position of the alphabets on the line, the child is introduced to sorting exercises. The exercise which gives the child the concept of grass letters, root letters and sky letters actually prepares the child indirectly for writing on paper since the child gets a clear concept of the place and position of each alphabet on the line.
                                                  </p>
                                                  <p>The children are introduced to the proper placement of alphabets using equidistant four lines. A large mat with four lines equidistant lines made on it with the help of ribbons or other such material mounted on it to make straight lines is used.
                                                  </p>
                                                  <p>The top and bottom lines are pink and the two center lines are aquamarine/blue (as on primary writing paper).
                                                  </p>
                                                  <h4 ref={grassLetter}>Grass letter</h4>
                                                  <p>Letters which fit exactly between the two blue middle lines for example, ‘a’, ‘c’, ‘e’, ‘i’, ‘o’, etc. are known as the grass letters.
</p>
<img src="https://raisingthemright.weebly.com/uploads/3/8/0/4/3804886/3290232_orig.jpg" alt="Picture"></img>
<h4 ref={skyLetter}>Sky Letters
</h4><p>Letters having a stem which goes up to the pink line are called sky letters, for example, ‘b’, ‘d’, ‘h’, ‘k’ etc. are known as the sky letters.
</p><img src="https://raisingthemright.weebly.com/uploads/3/8/0/4/3804886/4976625_orig.jpg" alt="" />
<h4 ref={rootLetter}>Root Letters
</h4><p>Letters having a stem which goes down to the pink line at the bottom for example, ‘g’, ‘j’, ‘p’, ‘q’, ‘y’ etc. are known as root letters.
</p><img src="https://raisingthemright.weebly.com/uploads/3/8/0/4/3804886/9195964_orig.jpg" alt="" />
                                        </div>
                              </div>

                    </main>
          )
}

export default InfoPage