import React from 'react'
import { BrowserRouter as Router , Routes,Route } from "react-router-dom";
import gamePage from './pages/gamePage';
import InfoPage from './pages/InfoPage';



function App() {
  return (
    <>
  <Router basename='/react-quiz-game1'>
    <Routes>
      <Route path='/react-quiz-game1' element={<game1Page/>}/>
      <Route path='/' element={<game1Page/>}/>
      <Route path='/react-quiz-game1/infopage' element={<InfoPage/>}/>
    </Routes>
  </Router>
    
    </>
  )
}

export default App